.wrapper {
  max-width: 1280px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
}

header {
  background: url("../img/head_bg.jpg") no-repeat;
  background-size: cover;
  width: 100%;
  position: relative;
  overflow: visible;
}

header .header__background {
  height: 100vh;
  padding-bottom: 20px;
}

header .header__background .header__top {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
  justify-content: space-between;
}

header .header__background .header__top .header__logo {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 5px;
  margin-right: 5px;
  width: calc(20.833333333333336% - 10px);
  padding-top: 50px;
  height: auto;
  overflow: hidden;
  text-align: center;
}

header .header__background .header__top .header__logo img {
  display: block;
  max-width: 100%;
  height: auto;
  margin: 0 auto;
}

header .header__background .header__top .header__siteName {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 5px;
  margin-right: 5px;
  width: calc(58.333333333333336% - 10px);
  font: 30px Neucha, cursive;
  color: #3d1b31;
  text-align: center;
  padding-top: 100px;
}

header .header__background .header__top .header__nav {
  width: calc(20.833333333333336% - 10px);
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 5px;
  margin-right: 5px;
  margin-top: 40px;
}

header .header__background .header__top .header__nav ul {
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
}

header .header__background .header__top .header__nav ul li {
  min-width: 200px;
  width: calc(100% - 10px);
}

header .header__background .header__top .header__nav ul li a {
  text-decoration: none;
  color: #3d1b31;
  font: 25px Neucha, cursive;
  margin: 10px 0;
  display: block;
  background-color: rgba(255, 255, 255, 0.9);
  border: 1px solid #8d48b4;
  background: rgba(255, 255, 255, 0.9);
  border-radius: 10px;
}

header .header__background .header__top .header__nav ul li a:hover {
  transform: scale(1.2);
}

header .header__background .header__top .header__nav ul li a img {
  max-width: 20px;
  height: auto;
  display: inline-block;
  margin: 0 10px;
}

header .header__background .header__bottom {
  max-width: 600px;
  text-align: center;
  margin: 50px auto;
  border: 2px solid #8d48b4;
  background: rgba(255, 255, 255, 0.9);
  padding: 20px 0;
  border-radius: 5px;
}

header .header__background .header__bottom .header__name {
  font: 45px Neucha, sans-serif;
  color: #3d1b31;
}

header .header__background .header__bottom .header__skills {
  font: 30px Neucha, sans-serif;
  color: #3d1b31;
}

main {
  background: #fcfcfc;
}

main .about {
  padding-top: 70px !important;
}

main .section {
  padding-top: 40px;
}

main .section::after {
  content: "";
  display: block;
  width: 50%;
  height: 2px;
  background: #8d48b4;
  margin: 20px auto;
}

main .section__title {
  font: 30px Neucha, cursive;
  margin: 0 auto;
  text-align: center;
  position: relative;
  opacity: 0;
}

main .section__title::after {
  content: "";
  display: block;
  width: 20%;
  height: 2px;
  background: #8d48b4;
  margin: 20px auto;
}

main .section__title::before {
  content: "";
  display: block;
  width: 10px;
  height: 10px;
  border-radius: 10px;
  background: #8d48b4;
  position: absolute;
  bottom: 120%;
  left: 50%;
  margin-left: 0;
}

main .section__content {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
  justify-content: space-around;
  align-items: stretch;
}

main .section__content .section__my-foto {
  width: calc(50% - 10px);
  opacity: 0;
}

main .section__content .section__my-foto .section__foto {
  max-width: 400px;
  max-height: 400px;
  overflow: hidden;
  margin: 0 auto;
  border-radius: 300px;
  box-shadow: 0 0 0 3px #8d48b4, 0 0 15px #333;
  margin: 0 10px;
}

main .section__content .section__my-foto .section__foto img {
  max-width: 100%;
  height: auto;
  display: block;
}

main .section__content .section__info {
  width: calc(50% - 10px);
  opacity: 0;
  font: 25px Neucha, cursive;
  color: #3d1b31;
}

main .section__content .section__info p {
  padding: 10px;
}

main .section__content .section__item {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 5px;
  margin-right: 5px;
  width: calc(33.333333333333336% - 10px);
  text-align: center;
  margin-bottom: 20px;
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
  flex-direction: column;
}

main .section__content .section__item a {
  display: block;
  text-decoration: none;
  font: 16px Neucha, cursive;
  color: black;
}

main .section__content .section__item h3 {
  display: inline-block;
  margin: 20px auto;
  font: 150% Neucha, cursive;
  color: #3d1b31;
  margin-bottom: auto;
}

main .section__content .section__item figure {
  margin: 15px 15px 0 15px;
  font: 130% Neucha, cursive;
  background: #f4f4f4;
  padding: 10px;
  border: 1px solid #8d48b4;
  border-radius: 5px;
  height: 370px;
}

main .portfolio__title {
  opacity: 0;
}

main .portfolio__content {
  opacity: 0;
}

main .service {
  padding-top: 60px;
}

main .service::after {
  content: "";
  display: block;
  width: 50%;
  height: 2px;
  background: #8d48b4;
  margin: 50px auto;
}

main .service__title {
  max-width: 300px;
  font: 30px Neucha, cursive;
  margin: 0 auto;
  text-align: center;
  position: relative;
  opacity: 0;
}

main .service__title::after {
  content: "";
  display: block;
  width: 70%;
  height: 2px;
  background: #8d48b4;
  margin: 20px auto;
}

main .service__title::before {
  content: "";
  display: block;
  width: 10px;
  height: 10px;
  border-radius: 10px;
  background: #8d48b4;
  position: absolute;
  bottom: 120%;
  left: 50%;
  margin-left: 0;
}

main .service__slider {
  opacity: 0;
}

main .service .container {
  margin-top: 40px;
}

main .contact__title {
  opacity: 0;
}

main .contact__item {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
  width: 100%;
  margin: 20px 0;
  font: 130% Neucha, cursive;
}

main .contact__item a {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
  text-decoration: none;
  color: black;
  width: 70%;
  margin: 0 auto;
  height: 30px;
  line-height: 30px;
}

main .contact__item a p {
  min-width: 300px;
}

main .contact__item a p:hover {
  transform: scale(1.1);
}

main .contact__item a .contact__icon {
  width: 30px;
  height: 30px;
  margin-right: 30px;
}

main .contact__item a .contact__icon img {
  display: block;
  width: 100%;
  height: 100%;
}

main .contact__content {
  opacity: 0;
}

main .button {
  background: url(../img/arrow1.png) 0 0 / cover;
  width: 50px;
  height: 50px;
  position: fixed;
  right: 20px;
  bottom: 20px;
  display: none;
  cursor: pointer;
}

/* Увеличение картинки на странице */

.minimized {
  cursor: pointer;
  border: 1px solid #FFF;
}

.minimized:hover {
  border: 1px solid #8d48b4;
}

/* увеличенная картинка */

#magnify {
  display: none;
  position: fixed;
  max-width: 80%;
  height: auto;
  z-index: 9999;
}

#magnify img {
  width: 100%;
}

/* затемняющий фон */

#overlay {
  display: none;
  background: #000;
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  opacity: 0.5;
  z-index: 9990;
}

/* кнопка закрытия */

#close-popup {
  width: 30px;
  height: 30px;
  background: #FFFFFF;
  border: 1px solid #AFAFAF;
  border-radius: 15px;
  cursor: pointer;
  position: absolute;
  top: 15px;
  right: 15px;
}

#close-popup:hover {
  animation: rota 4s infinite normal;
  -webkit-animation-iteration-count: 1;
  animation-iteration-count: 1;
}

#close-popup i {
  width: 30px;
  height: 30px;
  background: url(https://codernote.ru/files/cross.png) no-repeat center center;
  background-size: 16px 16px;
  display: block;
}

@keyframes rota {
  25% {
    transform: rotate(360deg);
  }
}

@font-face {
  font-family: 'Neucha';
  src: url('../fonts/neucha/Neucha.ttf') format('ttf'), url('../fonts/neucha/neucha-webfont.woff2') format('woff2'), url('../fonts/neucha/neucha-webfont.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

@media screen and (max-width: 992px) {
  .wrapper {
    padding-left: 20px;
    padding-right: 20px;
  }

  header .header__background .header__top .header__logo {
    width: calc(16.666666666666668% - 10px);
  }

  header .header__background .header__top .header__nav {
    width: calc(25% - 10px);
  }

  main .section__content .section__item {
    width: calc(50% - 10px);
  }
}

@media screen and (max-width: 800px) {
  .wrapper {
    padding-left: 10px;
    padding-right: 10px;
  }

  header .header__background .header__top .header__logo {
    width: calc(20.833333333333336% - 10px);
  }

  header .header__background .header__top .header__siteName {
    width: calc(79.16666666666667% - 10px);
  }

  header .header__background .header__top .header__nav {
    width: calc(100% - 10px);
  }

  header .header__background .header__top .header__nav ul {
    justify-content: space-around;
    max-width: 250px;
    margin: 0 auto;
  }

  header .header__background .header__top .header__nav ul li {
    width: calc(33.333333333333336% - 10px);
  }

  main .section__content .section__my-foto {
    width: calc(25% - 10px);
  }

  main .section__content .section__info {
    width: calc(75% - 10px);
  }

  main .section__content .section__info {
    font: 20px Neucha, cursive;
  }
}

@media screen and (max-width: 566px) {
  .wrapper {
    padding-left: 5px;
    padding-right: 5px;
  }

  header .header__background .header__top .header__nav ul {
    display: block;
    margin: 0 auto;
    max-width: 200px;
  }

  header .header__background .header__top .header__nav ul li {
    width: calc(100% - 10px);
  }

  main .section__content .section__my-foto {
    width: calc(62.50000000000001% - 10px);
  }

  main .section__content .section__info {
    width: calc(100% - 10px);
  }

  main .section__content .section__item {
    width: calc(100% - 10px);
  }
}

@media screen and (max-width: 523px) {
  main .contact__item a {
    width: 90%;
  }
}

@media screen and (max-width: 460px) {
  main .service .container .current {
    width: 90% !important;
    margin: 0 20px !important;
  }
}

@media screen and (max-width: 416px) {
  main .contact__item a {
    justify-content: center;
    height: auto;
  }

  main .contact__item a p {
    margin-top: 20px;
    text-align: center;
  }
}

@media screen and (max-width: 401px) {
  header .header__background {
    padding-bottom: 110px;
  }
}

@media screen and (max-width: 400px) {
  .wrapper {
    padding-left: 5px;
    padding-right: 5px;
  }

  header .header__background .header__top {
    justify-content: center;
  }

  header .header__background .header__top .header__logo {
    width: calc(41.66666666666667% - 10px);
  }

  header .header__background .header__top .header__siteName {
    width: calc(100% - 10px);
  }

  header .header__background .header__top .header__siteName {
    padding-top: 0;
  }

  main .service .container .current {
    width: 70% !important;
    margin: 0 60px !important;
  }
}

@media screen and (max-width: 323px) {
  header .header__background {
    padding-bottom: 150px;
  }
}

@media screen and (max-width: 320px) {
  main .service .container .current {
    margin: 0 40px !important;
  }
}